.article-module {
  width: 240px;
  margin-right: 16px;
  flex-shrink: 0;
}

.article-filters {
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 8px;
  border-bottom: 1px solid var(--color-divider);
  background-color: var(--color-secondary-bg);
  height: 48px;
  line-height: 48px;
  box-sizing: border-box;
}

.article-sort-types span {
  margin-left: 8px;
  color: var(--color-secondary-app);
}

.article-sort-types .active-sort-type {
  color: var(--color-brand);
}

.article-item {
  padding: 8px 12px;
  background-color: var(--color-secondary-bg);
}

.article-title {
  color: var(--color-primary);;
  font-weight: normal;
  font-size: 12px;
}

.article-detail {
  color: var(--color-thirdly);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
}
